/* Bread Crumbs */
.breadcrumbs {
    // background-color: #E8E8E8;
    position: relative;
    padding: 20px 0;
    // padding-top: 85px;
}

.breadcrumbs .breadcrumbs-content {
    position: relative;
    // padding: 20px;
}

.breadcrumbs .breadcrumbs-content p {
    color: $theme-color;
    font-size: 14px;
    // margin-top: 20px;
}

.breadcrumbs .breadcrumbs-content .page-title {
    font-size: 35px;
    color: $theme-color;
    font-weight: 700;
    position: relative;
    // padding-bottom: 22px;
    line-height: 50px;
}

// .breadcrumbs .breadcrumbs-content .page-title:before {
//     position: absolute;
//     content: "";
//     left: 50%;
//     bottom: 0;
//     height: 2px;
//     width: 80px;
//     background: $theme-color;
//     margin-left: -40px;
// }

.breadcrumbs .breadcrumbs-content .breadcrumb-nav {
    background: transparent;
    border-radius: 0;
    margin-bottom: 0;
    padding: 0;
    display: inline-block;
}

.breadcrumbs .breadcrumb-nav {
    // text-align: center;
    margin-top: 50px;
}

.breadcrumbs .breadcrumb-nav li {
    display: inline-block;
}

.breadcrumbs .breadcrumb-nav li,
.breadcrumbs .breadcrumb-nav li a {
    color: $theme-color;
    font-size: 14px;
    font-weight: 500;
}

.breadcrumbs .breadcrumb-nav li a {
    padding-right: 15px;
    margin-right: 15px;
    position: relative;
}

.breadcrumbs .breadcrumb-nav li a:hover {
    color: $theme-color;
}

.breadcrumbs .breadcrumb-nav li a:after {
    content: '';
    height: 80%;
    width: 2px;
    background-color: $theme-color;
    position: absolute;
    top: 2px;
    right: 0;
}

.section {
    padding-top: 100px;
    padding-bottom: 100px;
    position: relative;
}

/* Section Title */
.section-title {
    text-align: center;
    margin-bottom: 80px;
    padding: 0 300px;
    position: relative;
    z-index: 5;

    span {
        text-transform: uppercase;
        color: $theme-color;
        display: inline-block;
        margin-bottom: 10px;
        font-size: 14px;
    }

    h2 {
        font-size: 33px;
        margin-bottom: 18px;
        line-height: 50px;
        text-transform: capitalize;
        position: relative;
        font-weight: 700;
        padding-bottom: 14px;

        &:before {
            position: absolute;
            left: 50%;
            margin-left: -25px;
            bottom: -1px;
            height: 2px;
            width: 50px;
            background: $theme-color;
            content: "";
        }
    }

    p {
        font-size: 14px;
        line-height: 24px;
    }

    &.white-text {
        h2 {
            color: $white;

            &::before {
                background-color: $white;
            }
        }

        span {
            color: $white;
        }

        p {
            color: $white;
        }
    }
}



.section-title.align-right {
    padding: 0;
    padding-left: 600px;

    h2 {

        &:before {
            display: none;
        }

        &:after {
            position: absolute;
            right: 0;
            bottom: -1px;
            height: 2px;
            width: 50px;
            background: $theme-color;
            content: "";
        }
    }
}

.section-title.align-left {
    padding: 0;
    padding-right: 600px;

    h2 {
        &:before {
            left: 0;
            margin-left: 0;
        }
    }
}

/* One Click Scrool Top Button*/
.scroll-top {
    width: 50px;
    height: 50px;
    line-height: 50px;
    background: $theme-color;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    color: $white !important;
    border-radius: 0;
    position: fixed;
    bottom: 30px;
    right: 30px;
    z-index: 9;
    cursor: pointer;
    transition: all .3s ease-out 0s;
    border-radius: 50%;

    &:hover {
        -webkit-box-shadow: 0 1rem 3rem rgba(35, 38, 45, 0.15) !important;
        box-shadow: 0 1rem 3rem rgba(35, 38, 45, 0.15) !important;
        -webkit-transform: translate3d(0, -5px, 0);
        transform: translate3d(0, -5px, 0);
    }
}

/* Overlay */
.overlay {
    position: relative
}

.overlay::before {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0.4;
    background: $black;
    content: "";
    -webkit-transition: all 0.4s ease;
    -moz-transition: all 0.4s ease;
    transition: all 0.4s ease;
    z-index: 5;
}


/* Pagination CSS */
.pagination {
    text-align: left;
    margin: 65px 0 0 0;
    display: block;
}

.pagination.center {
    text-align: center;
}

.pagination.right {
    text-align: right;
}

.pagination .pagination-list {
    display: inline-block;
    border: 1px solid#eee;
    box-shadow: 0 10px 30px rgba(111, 111, 111, 0.1);
}

.pagination .pagination-list li {
    margin-right: -5px;
    display: inline-block;
}

.pagination .pagination-list li:last-child {
    margin-right: 0px;
}

.pagination .pagination-list li a {
    background: #fff;
    color: #081828;
    padding: 0 22px;
    font-weight: 500;
    font-size: 13px;
    border-radius: 0;
    line-height: 40px;
}

.pagination .pagination-list li.active a,
.pagination .pagination-list li:hover a {
    background: $theme-color;
    color: $white;
    border-color: transparent;
}

.pagination .pagination-list li a i {
    font-size: 20px;
}

.pagination .pagination-list li a i {
    font-size: 14px;
}

.blog-grids.pagination {
    margin-top: 50px;
    text-align: center;
}

.button .btn {
    display: inline-block;
    text-transform: capitalize;
    // font-size: .9375rem;
    font-weight: 400;
    padding: 15px 30px;
    background-color: $theme-color;
    color: $white;
    border: none;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
    transition: 0.5s;
    border-radius: 30px;
}

.button .btn:hover {
    background-color: $black;
    color: $white;
    box-shadow: 0 1rem 3rem rgba(35, 38, 45, 0.15) !important;
    transform: translate3d(0, -5px, 0);
}

.button .btn-alt {
    background-color: $black;
    color: $white;

    &:hover {
        background-color: $theme-color;
        color: $white;
    }
}

.align-left {
    text-align: left;
}

.align-right {
    text-align: right;
}

.align-center {
    text-align: center;
}



/* Preloader */
.preloader {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 999999999;
    width: 100%;
    height: 100%;
    background-color: #fff;
    overflow: hidden;
}

.preloader-inner {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}

.preloader-icon {
    width: 100px;
    height: 100px;
    display: inline-block;
    padding: 0px;
}

.preloader-icon span {
    position: absolute;
    display: inline-block;
    width: 100px;
    height: 100px;
    border-radius: 100%;
    background: $theme-color;
    -webkit-animation: preloader-fx 1.6s linear infinite;
    animation: preloader-fx 1.6s linear infinite;
}

.preloader-icon span:last-child {
    animation-delay: -0.8s;
    -webkit-animation-delay: -0.8s;
}

@keyframes preloader-fx {
    0% {
        transform: scale(0, 0);
        opacity: 0.5;
    }

    100% {
        transform: scale(1, 1);
        opacity: 0;
    }
}

@-webkit-keyframes preloader-fx {
    0% {
        -webkit-transform: scale(0, 0);
        opacity: 0.5;
    }

    100% {
        -webkit-transform: scale(1, 1);
        opacity: 0;
    }
}


/* Demos Area */

.demo-area {
    position: relative;
    background: $gray;
    overflow: hidden;
}

.demo-area .demo-inner {
    height: 700px;
    background-size: cover;
    background-position: center;
    position: relative;
    z-index: 3;
}

.demo-area .demo-inner::before {
    position: absolute;
    content: "";
    right: 0;
    width: 50%;
    background-image: url(../images/demo/demo-bg.jpg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    border-radius: 0;
    height: 100%;
    top: -100px;
    z-index: 1;
    border-radius: 0;
    border-radius: 0%;
    height: 800px;
    width: 800px;
    right: -135px;
    z-index: -1;
}

.demo-area .demo-text {
    float: none;
    margin-top: 220px;

    h1 {
        font-size: 35px;
