/*======================================
    Works CSS 作品列表
========================================*/
.wsite-content {

  ul {
    line-height: 1.7;
  }
}

.portfolio-section {
  &.section {
    padding-bottom: 70px;
  }

  .section-title {
    padding: 0;
  }

  .portfolio-btn-wrapper {
    margin-bottom: 0;
    text-align: right;
    position: absolute;
    right: 0;
    bottom: 0;
  }

  .section-title {
    margin-bottom: 0;
  }
}

.portfolio-button-head {
  width: 100%;
  height: 100%;
  position: relative;
}

.portfolio-section {
  .grid {
    // margin-top: 70px

  }

  .portfolio-btn-wrapper button {
    padding: 8px 20px;
    font-size: 13px;
    font-weight: 500;
    border: none;
    color: #051441;
    border-radius: 0;
    margin-right: 10px;
    margin-bottom: 10px;
    transition: all .3s ease-out 0s;
    background: 0 0;
    border: 1px solid #eee;
    border-radius: 30px;

    &:hover {
      background-color: #3763eb;
      border-color: transparent;
      color: #fff;
    }

    &.active {
      background: #3763eb !important;
      color: #fff;
      border-color: transparent;
    }

    &:last-child {
      margin-right: 0;
    }
  }
}

@media only screen and (min-width: 768px) and (max-width: 991px) {
  .portfolio-section .portfolio-btn-wrapper button {
    margin-right: 10px;
    padding: 7px 18px;
    font-size: 16px;
  }
}

@media (max-width: 767px) {
  .portfolio-section .portfolio-btn-wrapper button {
    margin-right: 10px;
    padding: 7px 18px;
    font-size: 16px;
  }
}

.portfolio-item-wrapper {
  position: relative;
  border-radius: 0;
  overflow: hidden;
  margin-bottom: 30px;

  &:hover .portfolio-overlay {
    opacity: 1;
    visibility: visible;
    -webkit-transform: scale(1);
    transform: scale(1);
  }

  .portfolio-img {
    overflow: hidden;
    position: relative;
    height: 235px;

    &::before {
      position: absolute;
      content: "";
      left: -50%;
      top: -50%;
      background-color: #fff;
      height: 0%;
      width: 0%;
      z-index: 5;
      opacity: 0;
      -webkit-transform: scale(0);
      transform: scale(0);
      transition: all .7s ease;
      -webkit-transition: all .7s ease;
      -moz-transition: all 0.7s ease;
    }
  }

  &:hover .portfolio-img::before {
    opacity: .2;
    -webkit-transform: scale(1);
    transform: scale(1);
    height: 100%;
    width: 100%;
    left: 0;
    top: 0;
  }

  .portfolio-img::after {
    position: absolute;
    content: "";
    right: -50%;
    bottom: -50%;
    background-color: #fff;
    height: 0%;
    width: 0%;
    z-index: 5;
    opacity: 0;
    -webkit-transform: scale(0);
    transform: scale(0);
    transition: all .7s ease;
    -webkit-transition: all .7s ease;
    -moz-transition: all 0.7s ease;
  }

  &:hover .portfolio-img::after {
    opacity: .2;
    -webkit-transform: scale(1);
    transform: scale(1);
    height: 100%;
    width: 100%;
    right: 0;
    bottom: 0;
  }

  .portfolio-img img {
    width: 100%;
    -webkit-transition: all .4s ease;
    transition: all .4s ease;
    -moz-transition: all .4s ease;
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
  }

  &:hover .portfolio-img img {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

.portfolio-section .pf-content {
  margin-top: 15px;

  .category {
    font-size: 1.125rem;
    color: #000;
    font-weight: 300;
  }

  h4 a {
    color: #081828;
    font-size: 1.5rem;
    font-weight: 500;
    margin-bottom: 10px;

    &:hover {
      color: inherit;
      text-decoration: underline;
    }
  }
}

.portfolio-details {
  .top-head {
    padding-bottom: 50px;
    padding-top: 50px;
    background-color: #f6f9fc;

    .cat {
      font-size: 14px;
      color: #9b9ba7;
      font-weight: 300;
      display: inline-block;
      margin-bottom: 10px;
    }

    h2 {
      margin-bottom: 40px;
      font-size: 35px;
      line-height: 65px;
      color: #081828;
      font-weight: 700;
      line-height: 55px;
    }

    .meta-description li {
      font-size: 15px;
      display: inline-block;
      margin-right: 50px;

      &:last-child {
        margin-right: 0;
      }

      .small-title {
        display: block;
        color: #081828;
        margin-bottom: 10px;
      }

      p {
        color: $theme-color
      }

      a {
        color: #9b9ba7;

        &:hover {
          text-decoration: underline;
        }
      }
    }
  }

  .content {
    padding: 100px 0;

    img {
      width: 100%;
      border-radius: 5px;
    }

    h3 {
      margin-top: 10px;
      margin-bottom: 20px;
      font-weight: 700;
      color: #081828;
      font-size: 26px;
    }

    p {
      margin: 30px 0;
      font-size: 15px;
      line-height: 28px;

      &:last-child {
        margin-bottom: 0;
      }
    }

    .share-social {
      margin-top: 40px;
      padding-top: 40px;
      border-top: 1px solid #eee;

      h5 {
        font-size: 18px;
        font-weight: 500;
        color: #081828;
        display: block;
        margin-bottom: 20px;
      }

      ul li {
        display: inline-block;
        margin-right: 25px;

        &:last-child {
          margin: 0;
        }

        a {
          color: #9b9ba7;
          font-size: 17px;

          &:hover {
            color: #3763eb;
          }
        }
      }
    }
  }
}

.portfolio-section {
  services {
    background-color: $white;
    position: relative;

    &_dec_1 {
      position: absolute;
      top: 0;
      left: 20px;
      z-index: 0;

      img {
        width: 200px;

      }

      @media #{$xs} {
        display: none;
      }
    }

    &_dec_2 {
      position: absolute;
      bottom: 0;
      right: 20px;
      z-index: 0;
      transform: rotate(180deg);

      img {
        width: 200px;
      }

      @media #{$xs} {
        display: none;
      }
    }
  }

}



