.marketing {
  padding: 20px;
}

.wsite-content {
  h2 {
    margin-bottom: 20px;
    font-size: 1.875rem;
    // font-weight: 300;
    color: $theme-color;
  }

  h4 {
    font-weight: 400;
    font-size: 1.25rem;
    margin: 10px 0;
  }

  p {
    // padding-bottom: 20px;
    font-size: 1.25rem;
    font-weight: 300;
  }

  ul {
    padding-left: 30px;
  }

  li {
    list-style-type: disc;
    // font-size: 1.25rem;
  }

  &-col {
    margin-bottom: 30px;
  }

  &-box {
    margin: 50px 0;

    .list-group {
      font-weight: 400;
      padding: 0;

      .link-dark {
        text-decoration: underline;
        font-size: 1.25rem;
      }

      li {

        list-style-type: none;
        color: $theme-color;

        a {
          // color: $theme-color;
          // text-decoration: underline;
        }
      }
    }

    .item {
      margin: 30px 0;
    }

    // 管理圖片
    figure {
      overflow: hidden;
      position: relative;
      height: 170px;

      img {
        width: 100%;
        transition: all .4s ease;
        -moz-transition: all .4s ease;
        transform: scale(1.1);
      }
    }
  }
}

.wsite-3Q {
  margin: 40px 0;
}

.next-page {
  position: relative;
  padding: 20px 30px;
  background-color: #fff;
  box-shadow: 0px 4px 19px rgba(0, 0, 0, 0.11);
  border-radius: 10px;

  &:hover {
    transition: all .3s ease-in;
    background-color: $theme-color2;
    transform: translate3d(0, -5px, 0);
  }

  h3 {
    font-size: 2.25rem;
    margin-bottom: 20px;
    @media #{$xs} {
      font-size: inherit;
   }
  }

  p {
    font-size: 1.125rem;
    color: #000;
  }

  i {
    font-size: 40px;
    margin: 0 10px;
    color: $theme-color;
  }

  &:before {
    content: "";
    position: absolute;
    width: 5px;
    height: 120px;
    // left: 0;
    background-color: $theme-color;
  }

}

.next-left {
  &:before {
    left: 0;
  }

}

.next-right {
  flex-direction: row-reverse;

  i {
    transform: rotate(180deg);
  }

  &::before {
    right: 0;
  }
}

